import React, { Component } from 'react';

class Dice extends Component {
  render() {
    const dice = this.props.value.map((v, i) => {
      return(
        <li key={i} className={'die number-' + v}></li>
      )
    });

    return(
      <ul className='dice'>{dice}</ul>
    )
  }
}

export default Dice;



